.profilePage {
  height: 82vh;

  .linkPackList {
    margin-top: 20px;
    display: flex;
    width: 100%;
    justify-content: flex-start;

    .link {
      color: black;
      text-decoration: none;
      font-weight: 500;
    }
  }

  .profileBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    width: 413px;
    height: 370px;
    background: #FFFFFF;
    box-shadow: 5px 5px 30px lightgray;
    margin-top: 30px;
    font-size: 30px;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;

    .userPhoto {
      height: 186px;
      width: 106px;
      border-radius: 50px;
      object-fit: cover;
    }

    .cardsCounts {
      font-size: 15px;
      font-weight: normal;
    }

    .photoIcon {
      width: 32px;
      position: relative;
      left: 35px;
      top: -30px;


    }

    .nameText {
      font-size: 20px;
      font-style: normal;
      padding-top: 0;

      .changeNameIcon {
        width: 15px;
        padding-left: 7px;
      }
    }

    .emailText {
      font-size: 14px;
      opacity: 50%;
    }
  }
}